// PrinterCard.js
import React from "react";
import {Box } from "@mui/material";
import { Card, CardContent, Button, Typography, Grid, CardActionArea } from "@mui/material";


import { CircleSharp} from "@mui/icons-material";
const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    pt: 2,
    px: 4,
    pb: 3,
  };

  const statusButtonStyle = {
    width: "20px",
    padding: "0px",// nh dng nt thnh hnh trn// Loi b padding  nt tr thnh hnh trn chnh xc
    position: "absolute", // t v tr tuyt i
    top: "5px", // t v tr  gc trn
    right: "10px", // t v tr  gc phi
    fontSize: "12px"
    // Thm cc thuc tnh khc theo nhu cu ca bn
  };
  

const PrinterCard = ({ printer, onSelect }) => {
    
  return (
    <Card onClick={() => onSelect(printer)} sx={{ position: "relative", padding: "15px" }}>
      <CardActionArea>
        <Grid container>
          <Grid item md={5} sx={{display: "flex",
                justifyContent: "center",
                alignItems: "center",
                width: "100%",}}>
            <Box>
              <img
                src="https://cdn.iconscout.com/icon/free/png-256/free-photocopier-machine-1125087.png"
                style={{ width: "100%", height: "100%", objectFit: "cover" }}
              />
            </Box>
          </Grid>

          <Grid item xs={12} md={7}>
            <CardContent>
              <Typography variant="h6">{printer.brand}</Typography>
              <Typography color="text.secondary">ID: {printer.id}</Typography>
              <Typography color="text.secondary">Location: {printer.location}</Typography>
              <Typography color="text.secondary">Status: {printer.status}</Typography>
            </CardContent>
            <Grid container justifyContent="flex-end">
                <Button color="warning" variant="contained" onClick={() => onSelect(printer)}>
                    Select
                </Button>
            </Grid>  
           </Grid>
        </Grid>
        <Box             sx={statusButtonStyle}> 
            <Button
            startIcon={ <CircleSharp /> }
            color={printer.status === "Online" ? "success" : "error"}

            />
        </Box>
        
      </CardActionArea>
    </Card>
  );
};

export default PrinterCard;
